export default {
	data() {
		return {
			commdity: [], //商品
			slideshow: [], //轮播
			show: false,
			popupimg:'',
			step:'1'
		}
	},
	onLoad(news) {
		// console.log(news)
		var item = JSON.parse(decodeURIComponent(news.news));
		console.log(item)
		this.commdity = item;
		this.slideshow = item.slideshow
		console.log(this.slideshow)
		this.popupimg = item.slideshow[0].slideshow
		// console.log(this.popupimg)
	},
	methods: {
		buy() {
			this.show = !this.show
		},
		onClose() {
			this.show = false
		},
		onChange(event) {
		    console.log(event.detail);
			this.step = event.detail;
		},
		next(){
			// var commdity = JSON.stringify(this.commdity);
			var commdity = this.commdity;
			var steps = this.step;
			uni.setStorageSync('comm',commdity)
			uni.setStorageSync('steps',steps)
			uni.navigateTo({
				url:'../ack_order/ack_order'
			})
		},
		shop(){
			uni.switchTab({
				url:'../shopping/shopping'
			})
		},
		shopping(){
			var commdity = this.commdity;
			var steps = this.step;
			// uni.setStorage({
			//     key: 'commdity',
			//     data: commdity,
			//     success: function () {
			//         console.log('success');
			//     }
			// });
			var that = this;
			let commlist = uni.getStorageSync('commlist');
			console.log(commlist)
			var comm={
				pid:this.commdity.pid,
				intro:this.commdity.intro,
				price:this.commdity.price,
				img:this.commdity.img,
				step:steps,
				check:false
			};
			commlist.push(comm)
				// for(let c in commlist){
				// 	if(commlist[c].pid == comm.pid){
				// 		// commlist.splice(c,1)
				// 		// console.log(1)
				// 	}
				// }
			console.log(commlist)
			uni.setStorageSync('commlist',commlist)
			uni.setStorageSync('steps',steps)
			uni.showToast({
			    title: '添加购物车成功',
			    duration: 2000
			});
		}
	}
}
